<template>
  <div>
    <div @click="clickEvent">这是测试页面</div>
    <iframe ref="iframeRef" src="https://dandanxu123.github.io/" id="iframeId"></iframe>
  </div>
</template>
<script lang="ts" setup>
import { onMounted, ref } from 'vue'
const iframeRef = ref()
const clickEvent = () => {
  var params = {
    type: "EventType",
    eventName: "animation1"
  }
  var iframeWindow: any = document.getElementById('iframeId');
  iframeWindow.contentWindow.postMessage(params, '*');
}

onMounted(() => {
  // /
})
</script>
<style scoped lang="less"></style>